interface ISubscription {
    type: string;
    handler: any;
}

export class ABus {

    private _subscriptions: Array<ISubscription> = [];

    public Handle(type: Function, handler: (massage: any) => void): ABus {

        this._subscriptions.push({
            type: type.name,
            handler: handler
        });

        return this;
    }

    public Send(message: any): ABus {

        this._subscriptions
            .filter(x => x.type == message.constructor.name)
            .forEach(x => x.handler(message));

        return this;
    }

    public SendAsync(message: any, timeout: number = 10): ABus {
        
        this._subscriptions
        .filter(x => x.type == message.constructor.name)
        .forEach(x => {
            let handler = x.handler;
            setTimeout(() => handler(message), timeout);
        });        

        return this;
    }

    public Unsubscribe(handler: (message: any) => void): ABus {
        
        this._subscriptions = this._subscriptions.filter(x => x.handler !== handler);

        return this;
    }
}



